body.dark h1, body.dark, p {color: #bee7ff} body.border{color:  rgb(45, 79, 90)} a{color: #bee7ff} input {color: #ccc !important} select {color: rgb(75, 123, 107)  !important;} textarea {color:rgb(127, 225, 255) !important;}

.checkbox {
  opacity: 0;
  position: absolute;
}



.checkbox-label {
  background-color: #868686;
  width: 57px;
  height: 26px;
  border-radius: 50px;
  position: relative;
  padding: 5px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.fa-moon {color: #ddd6bd; font-size: 16px;}

.fa-sun {color: #ddd6bd; font-size: 16px;}

.checkbox-label .ball {
  background-color: #1640ff;
  width: 24px;
  height: 27px;
  position: absolute;
  left: 0px;
  top: 0px;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

.checkbox:checked + .checkbox-label .ball {
  transform: translateX(32px);
}

.checkbox-label .ball::before {
  content: "\f185"; /* sun icon */
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 16px;
  margin-left: 3px;
  color: #ffe88d;
}


.checkbox:checked + .checkbox-label .ball::before {
  content: "\f186"; /* moon icon */
  color: #ffe88d;  
  margin-left: 3px;
}


