p {
    color: rgb(32, 84, 32);
}


#root > div{
    margin-top: 3vh;
}

li{
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
    border-color: #7272ff66;
    width: 60vw;
    padding-inline: 20px;
}


li:hover
{
    background-color: rgba(0, 255, 255, 0.13);
    transition: 0.1s;
}

@media screen and (max-width: 768px) {
    #root > div > div > div:nth-child(1){
        margin-top: 10vh;
    }
    button{
        display: none;
    }
}

